@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
html {
  font-size: 62.5%;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
}

.container {
  margin: auto;
  max-width: 1400px;
  height: 100%;
  display: flex;
  justify-content: center;
}

.sidebar__title {
  font-family: cambria, sans-serif;
  font-size: 2.4rem;
  font-weight: 400;
  opacity: .75;
  text-shadow: 1px 1px 3px rgb(248, 252, 6);
  text-align: center;
  padding: .6rem;
  margin-bottom: 1rem;
  letter-spacing: .2rem;
}
.balls__item {
  width: 70px;
  height: 70px;
  margin: 1rem;
  text-align: center;
  line-height: 220%;
  color: rgb(42, 97, 12);
  text-shadow: 1px 1px 3px #5eff00;
  ;
  font-size: 2.8rem;
  font-weight: 700;
  font-family: cambria, sans-serif;
  border: rgb(0, 0, 0) solid 0.2rem;
  border-radius: 50%;
  box-shadow: 1px 1px 3px #5eff00;
  background-color: rgb(248, 252, 6);
}

.occurrence-item--ball {
  width: 30px;
  height: 30px;
  margin: .5rem 0;
  text-align: center;
  line-height: 145%;
  color: rgb(68, 27, 5);
  font-size: 1.8rem;
  font-weight: 400;
  font-family: cambria, sans-serif;
  border: #ff7b00 solid .18rem;
  border-radius: 50%;
}
.occurrence-item--numbers {
  color: rgb(68, 27, 5);
  font-family: 'Poppins', sans-serif;
  font-weight: 200;
  font-size: 1.8rem;
  padding: .6rem 0;
  margin-right: 1rem;
}

.occurrence-item--nr {
  font-weight: 600;
}

.occurrence-item--nr,
.occurrence-item--percentage {
  display: inline-block;
  margin-left: 1.2rem;
  line-height: 150%;
}
.occurrence-item {
    width: 150px;
    height: 40px;
    padding: 0 .5rem;
    display: flex;
    justify-content: left;
}
.occurrences-wrapper {
    display: flex;
    flex-wrap: wrap;
}
.sidebar {
    width: 300px;
    height: 100%;
}
.central-container {
    width: 600px;
    height: 100%;
}
.header {
  margin-top: 1rem;
  padding: 1rem;
  font-family: 'Pacifico', sans-serif;
  font-size: 3.2rem;
  font-weight: 200;
  color: rgb(42, 97, 12);
  text-shadow: 1px 1px 3px #5eff00;
  text-align: center;
}
.balls {
    padding: 2rem;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.buttons {
    height: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    position: relative;
}
.buttons__generator {
  width: 200px;
  height: 50px;
  cursor: pointer;
  padding: 1rem;
  border-style: none;
  border-radius: 1rem;
  background-color: transparent;
  border: #5eff00 solid 0.2rem;
  font-family: cambria, sans-serif;
  font-size: 1.8rem;
  text-shadow: 1px 1px 3px #5eff00;
}

.buttons__generator:hover {
  color: rgb(68, 27, 5);
  text-shadow: 1px 1px 3px #ff7b00;
  border: #ff7b00 solid 0.2rem;
}

.buttons__next {
  width: 70px;
  height: 70px;
  border-radius: 50%;
}

.buttons__disabled,
.buttons__disabled:hover {
  border: #c9c9c9 solid 0.2rem;
  color: #858585;
  text-shadow: 1px 1px 3px #c2c2c2;
}

.toggle-list {
  width: 50px;
  height: 30px;
  font-family: 'Poppins', sans-serif;
  font-size: 1.5rem;
  opacity: .85;
  border: none;
  background-color: transparent;
  cursor: pointer;
  position: absolute;
  bottom: -1.8rem;
  right: 15%;
}

.hide-btn {
  color: #d81010;
}

.show-btn {
  color: #4ec00b;
}

.hide-btn:hover,
.show-btn:hover {
  font-size: 1.7rem;
  letter-spacing: 1.2px;
}
.drown-nr-wrapper__bar--counter {
  position: absolute;
  left: 1rem;
  color: rgb(42, 97, 12);
  letter-spacing: normal;
}
.drown-nr-wrapper__bar {
    color: rgb(68, 27, 5);
    position: relative;
    padding: .5rem .5rem .5rem 4rem;
    margin: 1.2rem;
    font-family: cambria, sans-serif;
    font-size: 2rem;
    letter-spacing: 5px;
    text-align: right;
    border: solid 1px rgb(231, 223, 223);
    border-radius: 5px;
}
.drown-nr-wrapper {
    width: 75%;
    height: 100%;
    margin: 2rem auto;
}
